import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import NavList from "./NavList";



function NavBar() {
  const [NavListState, setNavListState] = React.useState(false);

  const NavControls = {
    curState: NavListState,
    toggleDrawer: (newVal = undefined) => {
      if (newVal !== undefined) {
        setNavListState(newVal);
      } else {
        setNavListState(!NavListState);
      }
    },
  };

  const iconClicked = () => {
    NavControls.toggleDrawer();
  };

  return (
    <nav>
      <AppBar position="static">
        <Toolbar>
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
            onClick={iconClicked}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Tools Toolbox
          </Typography>
          <Button color="inherit">Login</Button>
        </Toolbar>
      </AppBar>
      <NavList NavControls={NavControls} />
    </nav>
  );
}

export default NavBar;
